body {
  background: #fafafa;
  font-family: Montserrat;
}

:root {
  --scroll-behavior: smooth;
  --black-main: #111;
  --dark-purple-main: #1e1823;
  --orange-main: #fd9222;
  --white-main: #fafafa;
}

.container-blocks {
  max-width: 325px;
  margin-left: auto;
  margin-right: auto;
}

.container-blocks.adaptiv {
  width: 100%;
  padding-top: 72px;
}

.container {
  max-width: 335px;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  row-gap: 32px;
  display: flex;
}

.title-container {
  height: 204px;
  flex-direction: column;
  flex-shrink: 0;
  align-items: center;
  row-gap: 24px;
  display: flex;
}

.title {
  color: #111;
  text-align: center;
  letter-spacing: -.6px;
  text-transform: uppercase;
  font-family: Montserrat;
  font-size: 20px;
  font-style: normal;
  font-weight: 600;
  line-height: 24px;
}

span {
  color: #fd9222;
  letter-spacing: -.6px;
  text-transform: uppercase;
  font-family: Montserrat;
  font-size: 20px;
  font-style: normal;
  font-weight: 600;
  line-height: 24px;
}

.description {
  color: #111;
  text-align: center;
  letter-spacing: -.28px;
  margin: 0;
  font-family: Montserrat;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 18px;
}

.card-container {
  max-width: 335px;
  flex-direction: column;
  flex-shrink: 0;
  align-items: flex-start;
  gap: 20px;
  display: flex;
}

.card {
  width: 335px;
  height: 270px;
  flex-shrink: 0;
}

.milk-chocolate {
  background: #fd9222;
  border-radius: 15px;
}

.milk, .sweet {
  background: #fff;
  border: 1px solid #fd9222;
  border-radius: 15px;
}

.milk .content-title {
  color: #1e1823;
}

.nuts {
  background: #1e1823;
  border-radius: 15px;
}

.front-content {
  position: relative;
}

#card-picture {
  width: 224px;
  height: 171px;
  flex-shrink: 0;
  display: block;
  position: absolute;
  top: 28px;
  left: 56px;
}

.content-title {
  color: #fff;
  letter-spacing: -.28px;
  text-transform: uppercase;
  font-family: Montserrat;
  font-size: 14px;
  font-style: normal;
  font-weight: 600;
  line-height: 18px;
  position: absolute;
  top: 224px;
  left: 28px;
}

.content-title.second {
  color: #111;
}

.back-content {
  width: 279px;
  flex-direction: column;
  flex-shrink: 0;
  align-items: flex-start;
  gap: 14px;
  display: flex;
}

.back {
  height: 214px;
  background: #fff;
  border-radius: 15px;
  row-gap: 14px;
  padding: 28px;
}

.about {
  flex-direction: column;
  align-items: flex-start;
  gap: 8px;
  display: flex;
}

.about-card {
  color: #111;
  letter-spacing: -.54px;
  text-transform: uppercase;
  font-family: Montserrat;
  font-size: 18px;
  font-style: normal;
  font-weight: 600;
  line-height: normal;
}

.little-blocks {
  align-items: flex-start;
  gap: 6px;
  display: inline-flex;
}

.white {
  color: #fff;
  background: var(--unnamed, #fd9222);
  border-radius: 35px;
  justify-content: center;
  align-items: center;
  gap: 4px;
  padding: 6px 16px;
  display: flex;
}

.black {
  border: 1px solid var(--unnamed, #fd9222);
  background: var(--unnamed, #fafafa);
  border-radius: 35px;
  justify-content: center;
  align-items: center;
  gap: 4px;
  padding: 6px 16px;
  display: flex;
}

.rotate {
  transform-style: preserve-3d;
  transition: transform 3s;
  position: relative;
}

.card:hover .rotate {
  transform: rotateY(180deg);
}

.front, .back {
  width: 100%;
  backface-visibility: hidden;
  transform-style: preserve-3d;
  border-radius: 15px;
  flex-direction: column;
  transition: transform .8s ease-in-out;
  display: flex;
  position: absolute;
}

.front {
  transform: rotateY(180deg);
}

#card-picture, .content-title {
  transform-style: preserve-3d;
  transform: translateZ(170px);
}

@media screen and (min-width: 768px) {
  .container-blocks {
    max-width: 768px;
  }

  .container-blocks.adaptiv {
    padding-top: 100px;
  }

  .container {
    max-width: 704px;
    flex-shrink: 0;
    row-gap: 50px;
  }

  .title-container {
    height: 156px;
    width: 486px;
    flex-shrink: 0;
    gap: 28px;
  }

  .title, span {
    letter-spacing: -.72px;
    font-size: 24px;
    line-height: 28px;
  }

  .description {
    letter-spacing: -.28px;
    margin: 0;
    font-size: 14px;
    line-height: 18px;
  }

  .card-container {
    max-width: 704px;
    flex-flow: wrap;
    flex-shrink: 0;
    gap: 32px;
    display: flex;
  }

  .card {
    width: 335px;
    height: 270px;
    flex-shrink: 0;
  }
}

@media screen and (min-width: 1200px) {
  .container-blocks.adaptiv {
    padding-top: 128px;
  }

  .container {
    max-width: 1200px;
  }

  .title-container {
    flex-shrink: 0;
    gap: 28px;
  }

  .title, span {
    letter-spacing: -.72px;
    font-size: 24px;
    line-height: 28px;
  }

  .description {
    letter-spacing: -.28px;
    margin: 0;
    font-size: 14px;
    line-height: 18px;
  }

  .card-container {
    max-width: 1136px;
    flex-direction: row;
    flex-shrink: 0;
    align-items: flex-start;
    gap: 19px;
    display: flex;
  }

  .back, .front {
    width: 270px;
    height: 270px;
    justify-content: center;
    align-items: center;
    padding: 20px;
    display: flex;
  }
}

